
.results-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-line-pack: center;
        align-content: center;
    margin-bottom: 15px;
}
.google-map{
    background-color: #00d6b2;
    height: 300px;
    grid-column: 1/13;
    grid-row: 1;

    border-radius: 3px;
    -webkit-box-shadow: 3px 3px 3px darkgrey;
            box-shadow: 3px 3px 3px darkgrey;
}

.weather-info{
    margin-bottom: 5px;
    width: 100%;
    border-radius: 3px;
    -webkit-box-shadow: 3px 3px 3px darkgrey;
            box-shadow: 3px 3px 3px darkgrey;

}
.radar-gif{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    border-radius: 3px;

}

.unsplash-images{
    background-color: #fefefe;
    border-radius: 3px;
    -webkit-box-shadow: 3px 3px 3px darkgrey;
            box-shadow: 3px 3px 3px darkgrey;
    /*overflow: hidden;*/
}

.note{
    text-align: center;
    font-size: 0.5em;
    font-style: italic;
}



@media (min-width: 620px){
    .results-container{
        border: none;
        margin: 10px;
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: auto;
        grid-gap: 10px;
    }
    .weather-info{
        background-color: #fefefe;
        grid-column: 1/6;
        grid-row: 2/3;

        display: -ms-flexbox;

        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: justify;
            justify-content: space-between;

        border: 1px solid darkgrey;
        border-radius: 3px;
        -webkit-box-shadow: 3px 3px 3px darkgrey;
                box-shadow: 3px 3px 3px darkgrey;

    }

    .unsplash-images{
        background-color: #fefefe;
        grid-column: 6/13;
        grid-row: 2/3;

        border-radius: 3px;
        -webkit-box-shadow: 3px 3px 3px darkgrey;
                box-shadow: 3px 3px 3px darkgrey;
    }

    .radar-gif{
        margin: 5px 5px 5px 5px;
        background-color: transparent;
        justify-self: end;
        /*grid-column: 1/6;*/
        /*grid-row: 3;*/
    }

}




